import { Component } from "react";
import "./Register.css";

class Register extends Component {
  state = {
    id: "",
    pw: "",
    pw_check: "",
    nickname: "",
    hint: "",
    hint_answer: "",
    id_dup: false,
    nick_dup: false,
  };

  handleIdCheck = () => {
    var kor_pattern = /[-|-|-]/g;
    if (kor_pattern.test(this.state.id)) {
      alert('    ')
    }
    else {
      fetch('/api/search/id/' + this.state.id)
        .then(res => res.json())
        .then(data => !data ? (window.confirm("   ,  ?") ? this.setState({id_dup:true}) : this.setState({id_dup:false})) : alert(" ,  ~"))
    }
  };

  handleNickNameCheck = () => {
    var pattern = /[^-&^-&^-&^a-z&^A-Z&^0-9]/g;
    if (pattern.test(this.state.nickname)) {
      alert('    ')
    }
    else {
      fetch('/api/search/nickname/' + this.state.nickname)
        .then(res => res.json())
        .then(data => !data ? (window.confirm("   ,  ?") ? this.setState({nick_dup:true}) : this.setState({nick_dup:false})) : alert(" ,  ~"))
    }
  }

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    })
  };

  passwordChk = () => {
    let pw = document.getElementById("pw").value;
    let pw_check = document.getElementById("pw_check").value;

    if (pw === pw_check) return true;
    else return false;
  };

  handleRegister = () => {
    if (!this.state.id_dup) {
      alert("  ")
    }
    else if (!this.state.nick_dup) {
      alert("  ")
    }
    else if (!this.passwordChk()) {
      alert(" k ");
    }
    else if (this.passwordChk() && this.state.id_dup && this.state.nick_dup) {
      let chk = window.confirm(" ?");
      if (chk) {
      fetch('/api/regist', {
        method : 'POST',
        redirect : 'follow',
        headers : {
          'Content-Type' : 'application/json',
        },
        body : JSON.stringify({
          id: this.state.id,
          pw: this.state.pw,
          nickname: this.state.nickname,
          hint: this.state.hint,
          hint_answer: this.state.hint_answer,
        })
      }).then(res => res.json())
      .then(data => data ?
        (() => {
          alert(" ");
          window.location.href = '/login'
        })() : alert(" "))
      }
    }
  };

  handleIdCancel = () => {
    this.setState({
      id_dup : false
    })
  }
  handleNickNameCancel = () => {
    this.setState({
      nick_dup : false
    })
  }

  render() {
    return (
      <div className="row justify-content-center" id="register_main">
        <div className="input-group mb-3">
          <span className="input-group-text inputText register_tag"></span>
          {this.state.id_dup ? (
            <input
              type="text"
              disabled
              className="form-control"
              placeholder=" ."
              name="id"
              id="id"
              value={this.state.id}
            />
          ) : (
            <input
              type="text"
              className="form-control"
              placeholder=" ."
              name="id"
              id="id"
              onChange={this.handleChange}
            />
          )}
          {this.state.id_dup ? (
            <button
              type="button"
              className="btn btn-success"
              id="id_check_btn"
              onClick={this.handleIdCancel}
            >
               
            </button>
          ) : (
            <button
              type="button"
              className="btn btn-secondary"
              id="id_check_btn"
              onClick={this.handleIdCheck}
            >
              
            </button>
          )}
        </div>
        <div className="input-group mb-3">
          <span className="input-group-text inputText register_tag"></span>
          <input
            type="password"
            className="form-control"
            placeholder=" ."
            name="pw"
            id="pw"
            onChange={this.handleChange}
          />
        </div>
        <div className="input-group mb-3">
          <span className="input-group-text inputText register_tag"> </span>
          <input
            type="password"
            className="form-control"
            placeholder=" ."
            name="pw_check"
            id="pw_check"
            onChange={this.handleChange}
          />
        </div>
        <div className="input-group mb-3">
          <span className="input-group-text inputText register_tag"></span>
          {this.state.nick_dup ? (
            <input
              type="text"
              disabled
              className="form-control"
              placeholder=" ."
              name="nickname"
              id="nickname"
              value={this.state.nickname}
            />
          ) : (
            <input
              type="text"
              className="form-control"
              placeholder=" ."
              name="nickname"
              id="nickname"
              onChange={this.handleChange}
            />
          )}
          {this.state.nick_dup ? (
            <button
              type="button"
              className="btn btn-success"
              id="nickname_check_btn"
              onClick={this.handleNickNameCancel}
            >
               
            </button>
          ) : (
            <button
              type="button"
              className="btn btn-secondary"
              id="nickname_check_btn"
              onClick={this.handleNickNameCheck}
            >
              
            </button>
          )}
        </div>
        <div>
          <select className="form-select" name="hint" onChange={this.handleChange}>
            <option value="0" selected="selected">
               
            </option>
            <option value="1">  ?</option>
            <option value="2">  ?</option>
            <option value="3">  ?</option>
            <option value="4">  1?</option>
            <option value="5"> ?</option>
          </select>
        </div>
        <div className="input-group mb-3">
          <span className="input-group-text inputText register_tag"> </span>
          <input type="text" className="form-control" name="hint_answer" onChange={this.handleChange}/>
        </div>
        <div className="btn-toolbar rg_btn_group">
          <div className="d-grid gap-2 col-4 mx-auto">
            <a
              className="btn btn-danger btn-cancle"
              id="register_btn"
              href="/"
            >
              
            </a>
          </div>
          <div className="d-grid gap-2 col-4 mx-auto">
            <button
              type="button"
              className="btn btn-primary btn-rg"
              id="register_btn"
              onClick={this.handleRegister}
            >
              
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default Register;
